<template>
  <div class="index_page">
    <div class="crud">
      <router-link to="/crud-component">增删改查组件1 >>>></router-link>
    </div>
    <div>
      <router-link to="/calendar">日历组件 >>>></router-link>
    </div>
    <canvas id="csv" style="background-color: #000"></canvas>
  </div>
</template>

<script>
export default {
  name: "indexPage",
  mounted() {
    // eslint-disable-next-line prettier/prettier
    console.log('?')
    const cvs = document.querySelector("#csv");
    const ctx = cvs.getContext("2d");

    const { clientWidth: width, clientHeight: height } =
      document.documentElement;
    cvs.width = width;
    cvs.height = height;
    ctx.fillStyle = "#ffffff";
    const bgColors = Array.from(new Array(400)).map(() => {
      return {
        x: Math.random() * width,
        y: Math.random() * height,
        step: Math.random() * 2.5 + 0.5,
      };
    });

    const render = () => {
      ctx.clearRect(0, 0, width, height);
      ctx.beginPath();
      bgColors.forEach((v) => {
        v.y = v.y > height ? 0 : v.y + v.step;
        ctx.rect(v.x, v.y, 3, 3);
      });
      ctx.fill();
      requestAnimationFrame(render);
    };
    render();
  },
};
</script>
<style lang="scss">
.index_page {
  margin-top: 20px;
  margin-left: 20px;
  a {
    font-weight: bold;
    color: #ffffff;
    text-decoration: none;
  }
  a:hover {
    color: rgb(144, 252, 140);
  }
}
#csv {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
}
.crud {
  margin-bottom: 20px;
}
</style>
